import React, { useEffect, useState } from "react";
import { MenuFoldOutlined, MenuUnfoldOutlined } from "@ant-design/icons";
import {
  AiOutlineDashboard,
  AiOutlineShoppingCart,
  AiOutlineUser,
  AiOutlineBgColors,
  AiOutlineStock,
  AiOutlineWallet
} from "react-icons/ai";
import { RiCouponLine } from "react-icons/ri";
import { TbTruckDelivery } from "react-icons/tb";
import { CiDeliveryTruck } from "react-icons/ci";
import { toast, ToastContainer } from "react-toastify";
import { Link } from "react-router-dom";
import { Outlet } from "react-router-dom";
import { ImBlog } from "react-icons/im";
import { IoIosNotifications, IoMdLogIn } from "react-icons/io";
import {
  FaClipboardList,
  FaBloggerB,
  FaPowerOff,
  FaImage,
  FaTags,
  FaUnity,
  FaTextHeight,
} from "react-icons/fa";
import { SiBrandfolder } from "react-icons/si";
import { BiCategoryAlt, BiSolidCategoryAlt } from "react-icons/bi";
import { GrDocumentUser } from "react-icons/gr";

import { FcFeedback, FcLeave } from "react-icons/fc";
import { Layout, Menu, message, Modal, theme } from "antd";
import { useNavigate } from "react-router-dom";
import {
  MdAddShoppingCart,
  MdOutlineAddBusiness,
  MdOutlineTitle,
} from "react-icons/md";
import axios from "axios";
import ScrollToTop from "../../scrolltotop";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
const { confirm } = Modal;
const { Header, Sider, Content } = Layout;
const API_BASE_URL = "https://majesticmall.net/api/admin";

const MainLayout = () => {
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedAdmin = Cookies.get("admin") || localStorage.getItem("admin");
  // console.log("encryptionKey",encryptedAdmin,encryptionKey);

  const admin = encryptedAdmin
    ? JSON.parse(
      CryptoJS.AES.decrypt(encryptedAdmin, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    )
    : null;

  const logOut = () => {
    toast.info("Successfully logout");
    Cookies.remove("admin");
    localStorage.removeItem("admin");
    setTimeout(() => {
      window.location.assign("/admin");
    }, 1000);

    // toast.info("Successfully logout");
  };

  const showDeleteConfirm = (id) => {
    confirm({
      title: "Are you sure you want to log out?",
      content: "This action can not be access dashboard.",
      okText: "Yes",
      okType: "danger",
      cancelText: "No",
      onOk() {
        logOut(id);
      },
      onCancel() {
        // console.log("Logout cancelled");
      },
    });
  };

  const [collapsed, setCollapsed] = useState(false);
  const {
    token: { colorBgContainer },
  } = theme.useToken();
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);
  const [subcategories, setsubCategories] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [customers1, setCustomers1] = useState([]);
  const [orders, setOrders] = useState([]);
  const [subscriptions, setSubscriptions] = useState([]);
  const [products, setProducts] = useState([]);
  const [products1, setProducts1] = useState([]);
  const [driver, setdriver] = useState([]);
  const [drivercheck, setdrivercheck] = useState([]);
  const [leaves, setleaves] = useState([]);
  const [coupon, setcoupon] = useState([]);
  const [feedback, setfeedback] = useState([]);
  const [testimonial, settestimonial] = useState([]);
  const [abandoned, setabandoned] = useState([]);
  const [text, settext] = useState([]);
  const [banner, setbanner] = useState([]);
  const [tag, settag] = useState([]);
  const [title, settitle] = useState([]);
  const [units, setunits] = useState([]);
  const [disbanner, setdisbanner] = useState([]);
  const [delivery, setdelivery] = useState([]);
  const [clubbanner, setclubbanner] = useState([]);

  const [orders1, setOrders1] = useState([]);
  const [profileData, setProfileData] = useState({});
  const getprofile = async () => {
    try {
      let res = await axios.get(
        "https://majesticmall.net/api/admin/getbussinessprofile"
      );
      if (res.status == 200) {
        setProfileData(res.data.profile);
      }
    } catch (error) {
      console.log(error);
    }
  };

  // const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
    : null;
  useEffect(() => {
    getprofile();
    fetchCategories();
    fetchSubCategories();
  }, []);
  const fetchCategories = async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/getCategory`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setCategories(response.data.category);
    } catch (error) {
      message.error("Failed to fetch categories");
    }
  };
  const fetchSubCategories = async () => {
    try {
      const response = await axios.get(`${API_BASE_URL}/getSubcategory`);
      setsubCategories(response.data.Subcategory);
    } catch (error) {
      message.error("Failed to fetch sub-categories");
    }
  };
  useEffect(() => {
    const fetchData = async () => {
      try {
        const [
          usersRes,
          ordersRes,
          subscriptionsRes,
          productres,
          fruitres,
          driverres,
          drivercheckres,
          leaveres,
          couponres,
          feedbackres,
          testimonialres,
          abandonedres,

          textres,
          banneres,
          Titleres,
          tagres,
          unitres,
          disbannerres,
          deliveryres,
          clubres,
        ] = await Promise.all([
          axios.get("https://majesticmall.net/api/user/getAllUser", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/user/getallOrder", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/user/getallSubscriptions", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/Getproductlist", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/Getfruitslist", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/user/getAlldriver", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/user/getAllCheckin", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/user/getAllleaves", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getallpromocode", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getallfeedback", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getTestimonials", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getAbandoned", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getText", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getallbanner", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getTitle", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getTag", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getUnit", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getalldiscountbanner", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getalldeliverycharges", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
          axios.get("https://majesticmall.net/api/admin/getallclubbanner", {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }),
        ]);

        setCustomers(usersRes.data.success);
        setProducts(productres.data.ProductList);
        setProducts1(fruitres.data.fruitsList);
        setdriver(driverres.data.success);
        setdrivercheck(drivercheckres.data.success);
        setleaves(leaveres.data.success);
        setcoupon(couponres.data.promocode);
        setfeedback(feedbackres.data);
        settestimonial(testimonialres.data.testimonials);
        setabandoned(abandonedres?.data?.abandonedCarts);
        settext(textres.data.Text);
        setbanner(banneres.data.banner);
        settitle(Titleres.data.Title);
        settag(tagres.data.Tag);
        setunits(unitres.data.Unit);
        setdisbanner(disbannerres.data.banner);
        setdelivery(deliveryres.data.deliveryCharges);
        setclubbanner(clubres.data.clubbanner);

        setOrders1(
          ordersRes.data.success.filter(
            (data) => data.orderType === "scheduled"
          )
        );
        setCustomers1(
          usersRes.data?.success?.filter((user) => user.club === true)
        );
        setOrders(
          ordersRes.data?.success?.filter((data) => data.orderType === "normal")
        );
        setSubscriptions(subscriptionsRes.data.success);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, []);

  // if(!admin){
  //   return window.location.assign("/admin");
  //  }

  return (
    <Layout hasSider /* onContextMenu={(e) => e.preventDefault()} */>
      <ToastContainer />
      <Sider trigger={null} collapsible collapsed={collapsed}>
        <div className="logo">
          <h2 className="py-3 mb-0 text-center text-white fs-5">
            <span className="sm-logo">MM</span>

            <img
              src={`https://majesticmall.net/profile/${profileData.businesslogo}`}
              loading="lazy"
              onError={(e) => {
                e.target.src = "../img/example.jpg"; // Replace with your default image URL
              }}
              style={{ marginTop: "-12px", width: "150px", height: "70px" }}
              className="lg-logo"
            />
          </h2>
        </div>
        <Menu
          theme="dark"
          mode="inline"
          defaultSelectedKeys={[""]}
          onClick={({ key }) => {
            <ScrollToTop />;
            if (key == "signout") {
            } else {
              navigate(key);
              window.scrollTo(0, 0);
            }
          }}
          items={[
            {
              key: "",
              icon: <AiOutlineDashboard className="fs-4" />,
              label: "Dashboard",
            },
            {
              key: "text",
              icon: <FaTextHeight className="fs-4" />,
              label: `Text (${text.length})`,
            },
            {
              key: "banner",
              icon: <FaImage className="fs-4" />,
              label: `Banner (${banner.length})`,
            },
            {
              key: "business-profile",
              icon: <MdOutlineAddBusiness className="fs-4" />,
              label: `Business Profile`,
            },
            {
              key: "category",
              icon: <BiSolidCategoryAlt className="fs-4" />,
              label: `Category (${categories.length})`,
            },
            {
              key: "subcategory",
              icon: <BiSolidCategoryAlt className="fs-4" />,
              label: `Sub-Category (${subcategories.length})`,
            },
            {
              key: "clubcustomers",
              icon: <AiOutlineUser className="fs-4" />,
              label: `Club Members (${customers1.length})`,
            },
            {
              key: "customers",
              icon: <AiOutlineUser className="fs-4" />,
              label: `Customers (${customers.length})`,
            },
            {
              key: "wallet",
              icon: <AiOutlineWallet className="fs-4" />,
              label: `Customers wallet`,
            },
            {
              key: "Product",
              icon: <AiOutlineShoppingCart className="fs-4" />,
              label: "Product",
              children: [
                {
                  key: "title",
                  icon: <MdOutlineTitle className="fs-4" />,
                  label: `Title (${title.length})`,
                },
                {
                  key: "unit",
                  icon: <FaUnity className="fs-4" />,
                  label: `Units (${units.length})`,
                },
                {
                  key: "tag",
                  icon: <FaTags className="fs-4" />,
                  label: `Tags (${tag.length})`,
                },
                {
                  key: "disbanner",
                  icon: <FaImage className="fs-4" />,
                  label: `Discount Banner (${disbanner.length})`,
                },
                // {
                //   key: "product",
                //   icon: <AiOutlineShoppingCart className="fs-4" />,
                //   label: "Add Groceries",
                // },
                // {
                //   key: "fruits",
                //   icon: <AiOutlineShoppingCart className="fs-4" />,
                //   label: "Add Fruits and Vegetables",
                // },
                {
                  key: "list-product",
                  icon: <AiOutlineShoppingCart className="fs-4" />,
                  label: `Product List (${products.length})`,
                },
                {
                  key: "stock",
                  icon: <AiOutlineStock className="fs-4" />,
                  label: `Stock Management `,
                },
                {
                  key: "DeliveryChargesList",
                  icon: <TbTruckDelivery className="fs-4" />,
                  label: `DeliveryCharges (${delivery.length})`,
                },
              ],
            },
            {
              key: "Orders",
              icon: <FaClipboardList className="fs-4" />,
              label: "Orders",
              children: [
                {
                  key: "orders",
                  icon: <FaClipboardList className="fs-4" />,
                  label: `Normal (${orders.length})`,
                },
                {
                  key: "orders1",
                  icon: <FaClipboardList className="fs-4" />,
                  label: `Scheduled (${orders1.length})`,
                },
                {
                  key: "subscription",
                  icon: <FaClipboardList className="fs-4" />,
                  label: `Subscription (${subscriptions.length})`,
                },
              ],
            },
            {
              key: "Driver",
              icon: <CiDeliveryTruck className="fs-4" />,
              label: "Driver",
              children: [
                {
                  key: "Drivers",
                  icon: <CiDeliveryTruck className="fs-4" />,
                  label: `Delivery Partner (${driver.length})`,
                },
                {
                  key: "Checkins",
                  icon: <IoMdLogIn className="fs-4" />,
                  label: `Checkins (${drivercheck.length})`,
                },
                {
                  key: "leaves",
                  icon: <FcLeave className="fs-4" />,
                  label: `Leaves (${leaves.length})`,
                },
              ],
            },
            {
              key: "marketing",
              icon: <RiCouponLine className="fs-4" />,
              label: "Marketing",
              children: [
                {
                  key: "coupon-list",
                  icon: <RiCouponLine className="fs-4" />,
                  label: `Coupon (${coupon.length})`,
                },
              ],
            },

            {
              key: "enquiries",
              icon: <FcFeedback className="fs-4" />,
              label: `Feedback (${feedback.length})`,
            },
            {
              key: "testimonial",
              icon: <GrDocumentUser className="fs-4" />,
              label: `Testimonial (${testimonial.length})`,
            },
            {
              key: "clubbanner",
              icon: <FaImage className="fs-4" />,
              label: `Club Banner (${clubbanner.length})`,
            },

            {
              key: "AbandonedCartList",
              icon: <MdAddShoppingCart className="fs-4" />,
              label: `Abandoned Cart (${abandoned.length})`,
            },
          ]}
        />
      </Sider>
      <Layout className="site-layout">
        <Header
          className="d-flex justify-content-between ps-1 pe-5"
          style={{
            padding: "45px",
            background: colorBgContainer,
            // position:"sticky",
            // top:0,
            // zIndex:1
          }}
        >
          {React.createElement(
            collapsed ? MenuUnfoldOutlined : MenuFoldOutlined,
            {
              className: "trigger",
              onClick: () => setCollapsed(!collapsed),
            }
          )}

          <div className="gap-4 d-flex align-items-center">
            <div className="gap-3 d-flex align-items-center dropdown">
              <div>
                <img
                  width={32}
                  height={32}
                  src="../img/admin.png"
                  alt="layout"
                  loading="lazy"
                />
              </div>
              <div
                role="button"
                id="dropdownMenuLink"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                <h5 className="mb-0">{admin.name}</h5>
                <p className="mb-0">{admin.email}</p>
              </div>

              <FaPowerOff
                onClick={showDeleteConfirm}
                style={{
                  height: "auto",
                  lineHeight: "20px",
                  fontSize: "20px",
                  color: "red",
                  cursor: "pointer",
                }}
              />
            </div>
          </div>
        </Header>
        <Content
          style={{
            margin: "24px 16px",
            padding: 24,
            minHeight: 280,
            background: colorBgContainer,
          }}
        >
          <ToastContainer
            position="top-right"
            autoClose={250}
            hideProgressBar={false}
            newestOnTop={true}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            theme="light"
          />
          <Outlet />
        </Content>
      </Layout>
    </Layout>
  );
};
export default MainLayout;
